<template>
	<scroll-view scroll-y=ture>
		<view v-for="order in orders">
			{{order.date}}
		</view>
	</scroll-view>
</template>

<script>
	export default {
		data() {
			return {
				orders:[]
			}
		},
		methods: {
		},
		onPullDownRefresh(){
			for(let i=0;i<200;i++){
				this.orders.push({
					"date":`${i}-${i}-${i}`,
					"num":`${i}`.repeat(6),
				})
			}	
		}
	}
</script>

